@import "./Variables/variables.scss";

.main-screen {
  min-width: 1920px;
  min-height: 1080px;
  max-width: 1920px;
  max-height: 1080px;
}

.left-container {
  max-width: 970px;
  max-height: 900px;
  margin: 20px;
}

.right-container {
  max-width: 890px;
  max-height: 900px;
  margin: 20px 0px;
}

.header-container {
  background-color: $backgroundColor;
  font-family: $fontfamilyPrimary;
  color: $primary;
  font-style: normal;
  max-width: 1920px;
  max-height: 140px;
  min-width: 1920px;
  min-height: 140px;
  border-bottom-width: 2px;
  border-bottom-color: #555555;
  border-bottom-style: solid;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.35);

  .header {
    padding-left: 40px;
    padding-top: 20px;
    height: 140px;
    float: left;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    .h-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 34px;
      letter-spacing: 0.025em;
      animation: fadein 0.35s ease-in;
      &.warning {
        color: #ff0000;
      }
    }

    .menu-liner {
      padding-top: 10px;
      height: 64px;
      width: 1380px;
      display: flex;
      .liner-box {
        background: #1f252b;

        box-sizing: border-box;
        width: 452px;
        height: 60px;
        margin-right: 10px;
        display: flex;
        padding: 5px;
        .ml-text {
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          animation: fadein 0.35s ease-in;
          &.label {
            color: $primary;
            margin-bottom: 2px;
          }
          &.value {
            color: #8c8c8c;
            font-size: 18px;
          }

          &.warning {
            color: #ff0000;
            font-size: 18px;
          }
        }
        .train-icon {
          background: url("../Assets/train-icon.svg") no-repeat;
          height: 32px;
          width: 32px;
          margin-top: 8px;
        }
        .bus-icon {
          background: url("../Assets/bus-icon.svg") no-repeat;
          height: 32px;
          width: 32px;
          margin-top: 8px;
        }
        .road-icon {
          background: url("../Assets/road-icon.svg") no-repeat;
          height: 32px;
          width: 32px;
          margin-top: 8px;
        }
      }
    }
  }

  .headerClock {
    max-width: 165px;
    max-height: 56px;
    padding-top: 42px;
    padding-right: 40px;
    text-align: right;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    float: right;
    text-transform: uppercase;
  }
}

.weather-feed {
  display: flex;
  flex-direction: row;
}
.text-twitter {
  font-family: $fontfamilyPrimary;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;

  &.name {
    padding-left: 20px;
    color: $primary;
  }

  &.user {
    color: $secondary;
    padding-left: 10px;
  }

  &.tweet {
    padding-left: 20px;
    color: $primary;
    padding-top: 5px;
  }
  *.hash {
    padding-left: 5px;
    color: #3792cb;
    padding-top: 5px;
  }
}
.text {
  font-family: $fontfamilyPrimary;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  padding-right: 10px;

  &.label {
    color: $primary;
    display: inline-block;
  }
  &.label::first-letter {
    font-weight: 800;
  }
  &.value {
    color: $secondary;

    &.location {
      white-space: normal;
    }

    &.casualty-count {
      float: right;
    }

    &.incident-type {
      white-space: normal;
      width: 150px;
    }

    .pointer-default {
      margin-right: 10px;
    }

    .pointer-warning {
      color: #ff0000;
      margin-right: 10px;
    }
  }

  &.warning {
    color: $warning;
  }
  &.title {
    font-size: 20px;
    color: $primary;
    line-height: 24px;
    //white-space: normal;
    //margin-bottom: 10px;
    width: 300px;
  }

  &.high {
    color: #ff0000;
  }
  &.medium {
    color: #f0b042;
  }
  &.low {
    color: #00ffa7;
  }
}

.urgency-text {
  font-family: $fontfamilyPrimary;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;

  &.high {
    color: #ff0000;
  }
  &.medium {
    color: #f0b042;
  }
  &.low {
    color: #00ffa7;
  }
}

.right-dashboard-home {
  border: 2px $borderColor solid;
  background-color: $backgroundColor;
  max-height: 900px;
  width: 890px;

  .right-dashboard-head {
    font-family: $fontfamilyPrimary;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 29px;
    color: $primary;
    padding: 20px;
    border-bottom: 2px $borderColor solid;
    height: 72px;
    width: 890px;
  }
  .right-dashboard-container {
    max-width: 890px;
    min-width: 890px;
    min-height: 825px;
    max-height: 825px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}
.widget-container {
  max-height: 242px;
  max-width: 888px;
