.stat-box {
  background-color: $backgroundColorSecondary;
  border-radius: 2px;
  padding: 12px;
  display: flex;
  flex-direction: column;

  text-align: center;
  .value {
    color: white;
    font-family: "Teko";
    font-size: 67px;
    line-height: 65px;
  }
  .small {
    font-size: 35px;
  }
  .medium {
    font-size: 57px;
  }
  .large {
    font-size: 72px;
  }
  .label {
    color: $secondary;
    font-size: 12px;
    margin-top: -10px;
  }
  .rain-low {
    color: #00ffa7;
  }
  .rain-heavy {
    color: #ff0000;
  }
}

.accordion {
  width: 876px;
  height: 88px;
  // margin-left: -20px;
  // margin-top: -15px;
  border-left: none;
  border-right: none;
  border-top: 1px solid #555555;
  animation: fadein 0.35s ease-in;
}

.accordion__item + .accordion__item {
  border-top: 2px solid #555555;
  border-bottom: 2px solid #555555;
  animation: fadein 0.35s ease-in;
}

.accordion__button {
  background-color: #191a1c;

  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  display: flex;

  align-items: center;
  .title-contain {
    width: 300px;
    margin-right: 20px;
  }

  .severity-contain {
    margin-right: 20px;
    width: 100px;
  }

  .type-contain {
    margin-right: 35px;
    width: 200px;
  }

  .view {
    min-width: 148px;
    height: 32px;
    background: #222d39;
    border-radius: 4px;

    font-weight: 500;
    font-size: 24px;
    line-height: 34px;
    text-align: center;

    color: #ffffff;
  }
}

.accordion__button:before {
  color: #b0b0b0;
  display: inline-block;
  content: "";
  height: 14px;
  width: 14px;
  margin-right: 20px;
  border-bottom: 2px solid currentColor;
  border-right: 2px solid currentColor;
  transform: rotate(45deg);
}

.accordion__button[aria-expanded="true"]::before,
.accordion__button[aria-selected="true"]::before {
  transform: rotate(-135deg);
}

[hidden] {
  display: none;
}

.accordion__panel {
  padding: 20px;
  animation: fadein 0.35s ease-in;
  border: none;
  white-space: nowrap;

  .row {
    margin-bottom: 10px;
  }

  .hazard-ae {
    height: 100%;
  }

  .hazard {
    min-height: 50%;
  }
  .access-egress {
    min-height: 50%;
  }

  .casualties {
    display: block;
  }

  .e-resources {
    display: inline-block;
    min-width: 292px;

    .resources-dist {
      float: right;
    }
  }
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
