.div {
  display: inline-table;
  width: 2em;
}
.dotFront {
  animation: pulseDot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -0.4s infinite;
  vertical-align: middle;
  margin-left: -1.5em;
}

.dotBack {
  animation: pulseRing 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
  vertical-align: middle;
}

@keyframes pulseRing {
  0% {
    transform: scale(0.5);
  }
  80%,
  100% {
    opacity: 0;
  }
}

@keyframes pulseDot {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes echo {
  0% {
    padding: 0;
    margin: 0;
    opacity: 1;
  }

  100% {
    padding: 50px;
    margin: -50px;
    opacity: 0;
  }
}

@keyframes echo {
  0% {
    padding: 0;
    margin: 0;
    opacity: 1;
  }

  100% {
    padding: 100px;
    margin: -50px;
    opacity: 0;
  }
}

.waves {
  position: absolute;
  left: 50%;
  top: 50%;
}

.wave {
  width: 100px;
  position: absolute;
  border: 2px solid #fff;
  border-radius: 50%;
  display: inline-block;
  padding: 0;

  animation-name: echo;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;

  -webkit-animation-name: echo;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-out;
}

.wave:nth-of-type(2) {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
}

.wave:nth-of-type(3) {
  animation-delay: 0.8s;
  -webkit-animation-delay: 0.8s;
}

.pulse {
  animation: pulse-animation 2s infinite;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}

.ring-container {
  position: relative;
}
.pulse-icon {
  margin-top: 20px;
  margin-right: 10px;
}
.circle {
  height: 56px !important;
  width: 158px !important;
  background-color: #3c99d0 !important;
  border-radius: 30px !important;
  -webkit-border-radius: 30px !important;
  text-align: center !important;
  vertical-align: middle !important;
  margin: 0 !important;
  padding: 0 !important;

  position: absolute !important;
}
.ring-con{
  position:absolute;
  margin-left:150px;
}
.ringring {
  border: 2px solid #3c99d0;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  height: 56px;
  width: 158px;
  position: absolute;
  animation: pulsate 1.2s ease-out;
  animation-iteration-count: infinite;
  -webkit-animation: pulsate 1.2s ease-out;
  -webkit-animation-iteration-count: infinite;
  opacity: 0;
}
.ringring:nth-of-type(2) {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
}
.ringring:nth-of-type(3) {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
}

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.5);
    opacity: 0;
  }
}
@keyframes pulsate {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.5);
    opacity: 0;
  }
}
.button-text {
  color: white;
  font-family: Lato;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 54px;
  /* identical to box height */

  text-align: center;
}

.call-pulse {
  border: 2px solid #3c99d0;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  height: 56px;
  width: 56px;
  position: absolute;
  animation: pulsate-call 1.2s ease-out;
  animation-iteration-count: infinite;
  -webkit-animation: pulsate-call 1.2s ease-out;
  -webkit-animation-iteration-count: infinite;
  opacity: 0;
}
.call-pulse:nth-of-type(2) {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
}
.call-pulse:nth-of-type(3) {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
}
@-webkit-keyframes pulsate-call {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.4, 1.4);
    opacity: 0;
  }
}
@keyframes pulsate-call {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.4, 1.4);
    opacity: 0;
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.centered {
  display: flex;
  justify-content: center;
  align-items: center;
}
