@import "./Variables/variables.scss";

.activity-log {
  height: 380px;
  .activity-log-time {
    color: $white;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
    display: inline;
    width: 100px;
  }
  .activity-log-desc {
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
    display: inline;
    width: 320px;
    color: $secondary;
  }
  .activity-content-log {
    overflow-y: auto;
    height: 320px;
    display: flex;
    flex-direction: column;
    padding: 15px;
  }
}
.incident-map {
  background: url("../Assets/incident-map-no-label.png") no-repeat;
  background-position: center;
  //width: 780px;
  background-size: contain;
  height: 500px;
  .icon-address-1 {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: $secondarylineHeight;
    position: absolute;
    top: 105px;
    left: 160px;
  }
  .icon-address-2 {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    position: absolute;
    top: 60px;
    right: 100px;
    width: 165px;
  }
}
.incident-map-2 {
  @extend .incident-map;
  height: 400px;
}

.activity-cctv {
  height: 380px;
  position: relative;
  .activity-cctv-title {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
  .activity-cctv-content {
    .activity-cctv {
      overflow: hidden;
      width: 200px;
      height: 100px;
    }

    .activity-cctv-img-1 {
      overflow: hidden;
      width: 200px;
      height: 100px;
    }
    .activity-cctv-img-2 {
      background: url("../Assets/cctv2.png") no-repeat;
      width: 200px;
      height: 100px;
    }
    .activity-cctv-lv {
      max-width: 150px;
      margin-left: 20px;
    }
    .lv-button {
      background: url("../Assets/LVButton.svg") no-repeat;
      width: 80px;
      height: 40px;
    }
    .lv-button div {
      color: $backgroundColor;
      font-family: $fontfamilySecondary;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 36px;
      text-align: center;
      color: $white;
    }
    .text-cctv {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;

      position: absolute;
      right: 35px;
    }
    .lv-address {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
  }
}
.drone-capture {
  height: 448px;
  .drone-capture-title {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
  .drone-capture-img {
    background: url("../Assets/droneImg.svg") no-repeat;
    width: 400px;
    height: 220px;
  }
  .drone-capture-content {
    padding: 15px;
    .drone-capture-text {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      margin-right: 10px;
    }
    .drone-capture-value {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      margin-right: 5px;
    }
    .row {
      justify-content: space-between;
    }
    .stats-row {
      display: flex;
    }
    .drone-capture-1 {
      color: $white;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
    .drone-capture-2 {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
    .drone-capture-battery-text {
      color: #00ffa7;
    }
    .signal-icon {
      background: url("../Assets/signal-icon.png") no-repeat;
      height: 24px;
      width: 24px;
      margin-right: 7px;
    }
    .speed-icon {
      background: url("../Assets/speed-icon.png") no-repeat;
      height: 24px;
      width: 24px;
      margin-right: 7px;
    }
    .battery-icon {
      background: url("../Assets/battery-icon.png") no-repeat;
      height: 24px;
      width: 24px;
      margin-right: 7px;
    }
  }
}
.white-text-1 {
  font-family: Lato;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  display: block;
  margin-top: 10px;
  align-items: center;
  text-align: center;
  color: #ffffff;
}
.white-text-medium {
  font-family: Lato;
  font-style: normal;
  font-size: 18px;
  line-height: 19px;
  align-items: center;
  text-align: center;
  display: block;
  color: #ffffff;
}
.white-text-small {
  font-family: Lato;
  font-style: normal;
  font-size: 16px;
  line-height: 19px;
  align-items: center;
  text-align: center;
  display: block;
  color: #ffffff;
}

.citizen-watch-phone {
  .citizen-watch-phone-title {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
}
.location-data {
  height: 448px;
  .location-data-title {
    color: $primary;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
  ul {
    list-style: none;
    padding: 0;
    display: block;
    float: left;
  }
  .location-data-content {
    float: left;
    width: 100%;
    .location-data-content-1 {
      color: $white;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      display: inline;
    }
    .location-data-content-2 {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      color: $secondary;
      display: inline;
      float: right;
      margin: 0;
    }
  }
  .location-overview-left {
    //display: inline;
    float: left;
    width: 410px;

    .location-overview-desc {
      color: $white;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      margin: 0;
    }
    .location-overview-1 {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
    ul {
      list-style: disc;
      padding: 0;
      display: block;
      margin-left: $fontsizePrimary;
    }
    .location-overview-desc-text {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
  }
}

