.sensor-data {
  .sensor-data-title {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
  .white-text {
    color: $white;
  }
  .grey-text {
    color: $grey;
  }
  .padding-left {
    padding-left: 10px;
  }
  .psi-band-text0 {
    color: orange;
  }
  .psi-band-text1 {
    color: green;
  }
  .psi-band-text2 {
    color: orange;
  }
  .psi-band-text3 {
    color: red;
  }
  .psi-band-text4 {
    color: yellow;
  }

  .sensor-data-left {
    display: inline;
    float: left;
    width: 120px;
    height: 130px;

    .sensor-data-text {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: 24px;
    }
  }
  .sensor-data-middle {
    display: inline;
    float: left;
    padding-left: 0px;

    .circle-sensor-data {
      background: url("../Assets/circle.svg") no-repeat;
      width: 100px;
      height: 100px;
      text-align: center;
      .circle-sensor-content {
        width: 100px;
        padding: 10px;
        .circle-sensor-value1 {
          color: $primary;
          font-family: $fontfamilyPrimary;
          font-style: normal;
          font-weight: 500;
          font-size: 30px;
          line-height: $primarylineHeight;
        }
        .circle-sensor-value2 {
          font-family: $fontfamilyPrimary;
          font-style: normal;
          font-weight: 500;
          font-size: $fontsizePrimary;
        }
      }
    }
  }
  .sensor-data-right {
    display: inline;
    float: left;
    .sensor-data-right-text {
      color: #f0b042;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
    }
    .sensor-data-text {
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: 24px;
    }
  }
}
.situation-overview {
  height: 331px;
  .situation-overview-title {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
  }
  .situation-overview-left {
    display: inline;
    float: left;
  }
  .situation-overview-desc {
    color: $white;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
    margin: 0;
  }
  .situation-overview-desc-text {
    color: $secondary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
    margin-bottom: 5px;
  }
  .red-text {
    color: #ff0000;
    margin-left: 10px;
  }
  .orange-text {
    color: #f0b042;
    margin-left: 10px;
  }
  .border-left-default {
    position: absolute;
    background-color: #00ffa7;
    width: 4px;
    height: 20px;
  }
  .border-left-warning {
    position: absolute;
    background-color: #ff0000;
    width: 4px;
    height: 20px;
  }
  .officer-logo {
    background: url("../Assets/officerlogo.svg") no-repeat;
    width: 14px;
    height: 14px;
    margin-right: 5px;
  }
  ul {
    list-style: disc;
    padding: 0;
    display: block;
    margin-left: $fontsizePrimary;
  }
  .situation-overview-1 {
    color: $secondary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
  }
  .situation-overview-right {
    display: inline;
    float: right;
    //padding-left: 20px;
  }
  .situation-overview-button {
    width: 390px;
    height: 32px;
    background-color: #3c99d0;
    border-color: #3c99d0;
    border-radius: 5px;
    color: $white;
    font-size: 20px;
    text-align: center;
    font-family: Teko;
    font-style: normal;
    font-weight: 500;
    transition: background-color 0.3s, border-color 0.3s;
  }
  .accepted-text {
    width: 390px;
    height: 32px;
    background-color: #222d39;
    border-color: #222d39;
    border-radius: 5px;
    color: $white;
    font-size: 20px;
    text-align: center;
    font-family: Teko;
    font-style: normal;
    font-weight: 500;
    transition: background-color 0.3s, border-color 0.3s;
  }
  .predicted-content {
    margin: 20px 20px 13px 20px;
    height: 250px;
    .entry {
      margin: 0px;
      .red-text {
        margin-bottom: 10px;
        font-family: Lato;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: flex-end;
        color: #ff0000;
      }
      .white-text {
        margin-bottom: 10px;
        font-family: Lato;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: flex-end;
        color: #fff;
      }
      .big-white-text {
        margin-bottom: 10px;
        font-family: Lato;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: flex-end;
        color: #fff;
      }
      .grey-text {
        margin-bottom: 10px;
        font-family: Lato;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        /* or 120% */

        display: flex;
        align-items: flex-end;

        color: #b0b0b0;
      }
    }
  }
}
.button {
  width: 64px;
  height: 32px;
  background-color: #222d39;
  border-color: #222d39;
  border-radius: 5px;
  padding: 0px;
}
.time-text {
  font-family: "Lato";
  font-weight: 500;
  font-size: 20px;
  line-height: 27.32px;
  color: $white;
}
.button-row {
  position: absolute;
  bottom: 20px;
}
.run-simulation-button {
  width: 390px;
  height: 32px;
  background-color: #222d39;
  border-color: #222d39;
  border-radius: 5px;
  color: $white;
  font-size: 20px;
  text-align: center;
  font-family: Teko;
  font-style: normal;
  font-weight: 500;
  transition: background-color 0.3s, border-color 0.3s;
  margin-right: 20px;
}
.bg-CCTV {
  background-color: #ffffff !important;
}
.progress {
  background-color: #555555;
  width: 240px;
  height: 4px;
  border-radius: 0px;
  margin-top: 20px;
}
.progress-text {
  width: 240px;
  height: 24px;
  font-family: Lato;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  text-align: center;

  color: #b0b0b0;
}
.progress-bar-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.shift-left {
  margin-left: -20px;
}
