.container {
  position: relative;
  width: 430px;
  height: 666px;
  background: #191a1c;

  word-break: break-all;
  // border: 1px solid rgba(0, 0, 0, 0.274);
}

.bloc-tabs {
  // display: flex;
  //width: 460px;
  height: 62px;
  margin: 0;
}
.tabs {
  text-align: center;
  width: 223px;
  background: rgba(37, 5, 5, 0.075);
  cursor: pointer;
  position: relative;
  outline: none;
  border-bottom: 2px #555555 solid;
}
.tabs:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs {
  background: #222d39;
  width: 225px;
  border-bottom: 2px #3c99d0 solid;
}

.tab-content-title {
  color: #ffffff;
  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  padding: 15px 0px;

  margin-bottom: 0;
}

button {
  border: none;
}
.content-tabs {
  flex-grow: 1;
}
.content {
  //background: #191a1c;
  //padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
}
.content h2 {
  padding: 0px 0 5px 0px;
}
.content hr {
  width: 100px;
  height: 2px;
  background: #191a1c;
  margin-bottom: 5px;
}
.content p {
  width: 100%;
  //height: 100%;
}
.active-content {
  display: block;
}

.hospital-container {
  padding: 20px;

  border-bottom-width: 2px;
  border-bottom-style: solid;
  border-bottom-color: #555555;

  .h-name {
    font-family: Lato;
    font-style: normal;

    font-size: 21px;
    line-height: 24px;
    color: $primary;
  }
  .h-distance {
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    display: flex;
    align-items: center;
    text-align: right;
    color: $grey;
  }
  .capacity {
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: $grey;
    width: 60%;
    padding-top: 2px;
  }
  .capability {
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: $grey;
  }

  .tag {
    background-color: $borderColor;
    font-size: 12px;
    color: $primary;
    font-weight: 600;

    padding: 2px 7px 2px 7px;
    border-radius: 4px;
    text-align: center;
    margin-bottom: 10px;
    line-height: 18px;
    margin-right: 10px;
  }
}
.asset-container {
  border-bottom-width: 2px;
  border-bottom-style: solid;
  border-bottom-color: #555555;
}

.asset-stat {
  background-color: $backgroundColorSecondary;
  border-radius: 8px;
  padding: 5%;
  display: flex;
  flex-direction: column;
  text-align: center;
  .value {
    color: white;
    font-family: "Teko";
    font-size: 67px;
    line-height: 65px;
  }
  .text-unit {
    color: $white;
    font-family: $fontfamilyPrimary;
    font-size: $fontsizePrimary;
    margin-left: 10px;
  }
  .small {
    font-size: 35px;
  }
  .medium {
    font-size: 57px;
  }
  .label {
    color: $secondary;
    font-size: 12px;
    margin-top: -10px;
  }
}
.asset-list-item {
  padding: 20px;
  &:hover {
    background-color: #1e252c;
  }
  .h-name {
    font-family: Lato;
    font-style: normal;
    font-size: 20px;
    line-height: 24px;
    color: $primary;
  }
  .h-distance {
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    display: flex;
    align-items: center;
    text-align: right;
    color: $grey;
  }
  .col-lg-2 {
    padding-right: 0px !important;
  }
  .col-lg-4 {
    padding: 0px !important;
  }
  .col-lg-7 {
    padding-right: 0px !important;
  }
}
.asset-list-content {
  padding: 0px 20px 20px 20px;
}

.list-box {
  display: flex;
  flex-direction: flex;
  flex-wrap: wrap;
  width: 100%;
  .box-value {
    order: 2;
    width: 50%;
    padding: 5px;
  }
}
