.video-container {
  position: relative;
  display: grid;
  place-items: center;
  height: 220px;
  width: 100%;
  margin: 0 auto;
  background: #ccc;
  overflow: hidden;

  .fullscreen {
    position: absolute;
    z-index: 0;
    object-fit: cover; // this is the key
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &::-webkit-media-controls {
      display: none !important;
    }
  }
}
