.header-incident {
  background-color: $backgroundColor;
  .header-incident-content {
    height: 140px;
    padding-top: 10px;
    width: 100%;
    max-width: 1920px;

    .incident-top-left {
      .incident-left-top {
        padding: 20px;
        margin-left: 20px;
        float: left;

        .incident-top-address {
          color: $primary;
          font-family: $fontfamilyPrimary;
          font-size: 24px;
          font-style: normal;
          font-weight: 700;
          line-height: 30px;
          text-align: left;
        }

        .incident-second-address {
          color: $secondary;
          font-family: $fontfamilyPrimary;
          font-size: 16px;
          font-style: normal;
          font-weight: 500;
          line-height: 21px;
          letter-spacing: 0em;
          text-align: left;
        }

        .incident-third-address {
          //styleName: Teko-Small;
          color: $secondary;
          font-family: $fontfamilyPrimary;
          font-size: 16px;
          font-style: normal;
          font-weight: 500;
          line-height: 21px;
          letter-spacing: 0em;
          text-align: left;
        }
      }

      .incident-right-top {
        padding-left: 20px;
        padding-top: 15px;
        float: left;

        .classFire {
          background: url("../Assets/ClassFire.svg") no-repeat;
          width: 120px;
          height: 30px;
          float: left;

          .text {
            padding: 3px;
            height: 30px;
            text-align: center;
            font-size: 20px;
            line-height: 28px;
            font-weight: 500;
            font-family: $fontfamilySecondary;
            color: $white;
          }
        }

        .incident-type {
          color: $white;
          font-family: $fontfamilyPrimary;
          font-size: 16px;
          font-weight: 500;
          display: inline;
          line-height: 21px;
        }

        .type {
          color: $secondary;
          font-family: $fontfamilyPrimary;
          font-style: normal;
          font-size: 16px;
          font-weight: 500;
          padding-left: 65px;
          line-height: 21px;
          display: inline;
        }

        .red-text {
          color: #ff0000;
          font-family: $fontfamilyPrimary;
          font-size: 24px;
          line-height: 30px;
          padding-left: 60px;
        }

        .estimate-injury {
          color: $white;
          font-family: $fontfamilyPrimary;
          font-size: 16px;
          font-weight: 500;
          display: inline;
          line-height: 21px;
        }

        .injury-num {
          color: $secondary;
          font-family: $fontfamilyPrimary;
          font-style: normal;
          font-size: 16px;
          font-weight: 500;
          padding-left: 25px;
          line-height: 21px;
          display: inline;

          .injury-num-red {
            color: #ff0000;
          }
        }

        .key-personel {
          color: $white;
          font-family: $fontfamilyPrimary;
          font-size: 16px;
          font-weight: 500;
          display: inline;
        }

        .key-personel-desc {
          color: $secondary;
          font-family: $fontfamilyPrimary;
          font-style: normal;
          font-size: 16px;
          font-weight: 500;
          padding-left: 75px;
          line-height: 21px;
          display: inline;
        }
      }
    }

    .container-button {
      width: 620px;
      color: white;
      font-family: $fontfamilyPrimary;
    }

    .progressbar {
      counter-reset: step;
    }

    .greenbackground {
      background: url("../Assets/greenbg.svg") no-repeat;
      width: 64px;
      height: 30px;
      float: left;

      .text {
        text-align: center;
        font-size: 25px;
        line-height: 50px;
        color: $white;
      }
    }

    .progressbar li {
      position: relative;
      list-style: none;
      float: left;
      width: 33.33%;
      text-align: center;
      font-family: $fontfamilyPrimary;
      font-weight: 500;
      font-style: normal;
      line-height: 36px;
      font-size: 25px;
      color: $primary;
    }

    .progressbar a {
      color: $primary !important;
    }

    /* Circles */
    .progressbar li a p {
      //content: counter(step);
      //counter-increment: step;
      width: 40px;
      height: 40px;
      border: 1px solid $primary;
      display: block;
      text-align: center;
      margin: 0 auto 10px auto;
      border-radius: 50%;
      background-color: #1e252c;
      font-family: $fontfamilyPrimary;

      /* Center # in circle */
      line-height: 39px;
    }

    .progressbar li:after {
      content: "";
      position: absolute;
      width: 80%;
      height: 1px;
      background: $secondary;
      top: 20px; /*half of height Parent (li) */
      left: -40%;
      z-index: 1;
    }

    .progressbar li:first-child:after {
      content: none;
    }

    .progressbar li a.active p {
      background: #3c99d0;
      color: $white;
    }

    .progressbar li a:hover {
      text-decoration: none !important;
      p {
        background: #3c99d0;
        color: $white;
      }
      span {
        color: #3c99d0;
      }
    }
    .progressbar li a.active span {
      color: #3c99d0;
    }

    .progressbar li span {
      font-family: $fontfamilyPrimary;
      font-weight: 500;
      font-style: normal;
      line-height: 21px;
      font-size: 16px;
      color: $secondary;
    }

    .progressbar li a.active + li:after {
      background: $secondary;
    }
  }
}

