}

.floating-menu {
  position: fixed;
  bottom: 10px;
  right: 10px;

  .menu .expand {
    display: inline;
  }

  .menu .collapse {
    display: none;
  }

  .menu {
    background-color: $backgroundColor;
    border-style: solid;
    border-width: 1px;
    border-color: $borderColor;

    -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);

    .list-item {
      padding-left: 25px;
      padding-right: 25px;

      a {
        font-family: "Teko";
        font-size: 25px;
        color: $secondary;
      }
      a:hover {
        color: $actionColor;
      }
    }
  }
  .main-button {
    float: right;
    height: 40px;
    width: 40px;
    background-color: $backgroundColor;
    border-radius: 50%;

    -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);

    .C4-icon-expand {
      margin-top: 15px;
      margin-left: 15px;
    }
    .C4-icon-hide {
      margin-top: 15px;
      margin-left: 15px;
      transform: rotate(180deg);
    }
  }
}
