@import "./Variables/variables.scss";

.citizen-watch-container {
  color: $primary;

  .cw-header {
    height: 81px;
    margin-bottom: 16px;

    background: #191a1c;
    border-bottom-width: 2px;
    border-bottom-color: #555555;
    border-bottom-style: solid;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.35);

    .liner {
      padding-left: 40px;
      padding-right: 40px;
      padding-top: 20px;
      .cw-text {
        font-family: $fontfamilyPrimary;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;

        &.title {
          padding-right: 16px;
          font-weight: 700;
          font-size: 24px;
        }
        &.value {
          color: $secondary;
        }
      }
      .clock {
        color: #bfbfbf;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        float: right;
      }
    }
  }
}

.responderNearby-height {
  height: 534px;
  margin-bottom: 0px !important;
}
.responderNearby {
  margin-bottom: 30px;
}

.responderNearby,
.responderNearby-height {
  //min-height: 240px;
  //width: 480px;
  //margin-bottom: 30px;

  .responderNearby-head-row {
    //height: 56px;
    //padding-left: 20px;
    //padding-top: 11px;
    .responderNearby-title {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      margin-right: 50px;
    }

    .responderNearby-distance {
      color: $secondary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 34px;
      //padding-left: 55px;
    }
  }

  .responderNearby-content {
    border: none;
    //min-height: 182px;
    .training-title {
      margin-bottom: 2px;
      margin-top: -10px;
    }
    .block {
      height: 41px;
      width: 240px;
      padding: 8px 0px 8px 8px;
      &.gray {
        background-color: #373737;
      }

      .responders {
        float: right;
        .number {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          color: #bfbfbf;
          margin-right: 0px;
        }
        .alert-circle-icon {
          background: url("../Assets/alert-circle-default-icon.png") no-repeat;
          background-size: contain;
          padding-right: 20px;
          margin-left: 5px;
          margin-right: 5px;
          &.hidden {
            opacity: 0;
          }
        }
      }
    }
    .blink {
      animation: blink 1s steps(1, end) infinite;
    }

    @keyframes blink {
      0% {
        opacity: 1;
      }
      50% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }

    .stat-box {
      margin-top: 16px;
      height: 120px;
      padding: 15%;
    }

    .requestCR-button-disabled {
      margin-top: 25px;
      margin-bottom: -20px;
      width: 100%;
      height: 32px;
      font-family: Teko;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 40px;
      background: #222d39;
      border-radius: 4px;
      animation: fadein 0.35s ease-in;
    }

    .requestCR-button {
      background: #3c99d0;
      margin-top: 25px;
      margin-bottom: -20px;
      width: 100%;
      height: 32px;
      font-family: Teko;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 40px;
      border-radius: 4px;
      &:hover {
        background: #222d39;
      }
    }

    .sendRequestCR-button {
      background: #3c99d0;
      margin-top: 15px;
      margin-bottom: 3px;
      width: 100%;
      height: 32px;
      font-family: Teko;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 40px;
      border-radius: 4px;
      margin-left: -10px;
      &:hover {
        background: #222d39;
      }
    }

    .responderBottomBox {
      margin-top: 20px;

      border-top-width: 3px;
      border-top-color: rgba(83, 81, 81, 0.3);
      border-top-style: solid;
      width: 400px;
      .header-info {
        padding-bottom: 7px;
      }
    }
    .responderModal {
      width: 100%;
      margin-bottom: -15px;
      animation: fadein 0.35s ease-in;
      white-space: nowrap;

      .responderModalTitle {
        display: inline-block;
        //margin-left: 20px;
        margin-bottom: 18px;
        margin-right: 130px;
        margin-top: 8px;
      }
      .responderModalForm {
        margin-left: 20px;
      }
      .responderModalAccept {
        margin-left: 0px;
      }

      .responderModalForm,
      .responderModalAccept {
        //margin-left: 20px;
        .plusminusform {
          display: flex;
          margin-bottom: 20px;
        }

        .responderTable {
          margin-top: 20px;
          margin-left: 15px;
          .responder-title-row {
            margin-bottom: 15px;
            margin-left: 10px;
          }
          .responderTable-row {
            border: 1px solid #595959;
            border-left: none;
            border-right: none;
            padding: 9.5px;

            .william-ng-icon {
              background: url("../Assets/william-ng.png") no-repeat;
              background-size: contain;
              padding-right: 22px;
              margin-right: 15px;
              margin-left: -10px;
            }

            .patricia-lim-icon {
              background: url("../Assets/patricia-lim.png") no-repeat;
              background-size: contain;
              padding-right: 22px;
              margin-right: 15px;
              margin-left: -10px;
            }

            .amirul-faiz-icon {
              background: url("../Assets/amirul-faiz.png") no-repeat;
              background-size: contain;
              padding-right: 22px;
              margin-right: 15px;
              margin-left: -10px;
            }

            .responder-name {
              font-family: Lato;
              font-style: normal;
              font-weight: 500;
              font-size: 18px;
              line-height: 22px;
            }

            .Accepted {
              color: #ffc700;
              .icon {
                background: url("../Assets/accepted-icon.png") no-repeat;
                background-size: contain;
                margin-right: 8px;
                padding-right: 22px;
              }
            }
            .Received {
              .icon {
                background: url("../Assets/received-icon.png") no-repeat;
                background-size: contain;
                margin-right: 8px;
                padding-right: 22px;
              }
            }
            .Viewed {
              .icon {
                background: url("../Assets/viewed-icon.png") no-repeat;
                background-size: contain;
                margin-right: 8px;
                padding-right: 22px;
              }
            }
            .On-hold {
              color: #8c8c8c;
              .icon {
                background: url("../Assets/onhold-icon.png") no-repeat;
                background-size: contain;
                margin-right: 8px;
                padding-right: 22px;
              }
            }
            .Arrived {
              color: #00ffa7;
              .icon {
                background: url("../Assets/arrived-icon.png") no-repeat;
                background-size: contain;
                margin-right: 8px;
                padding-right: 22px;
              }
            }
          }
        }

        .alertMessage {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #177ddc;
        }
        .alert-circle-secondary-icon {
          background: url("../Assets/alert-circle-secondary-icon.png") no-repeat;
          background-size: contain;
          padding-right: 20px;
          margin-right: 9px;
        }

        .text-counter {
          color: $white;
          font-size: 18px;
          font-family: $fontfamilyPrimary;
          font-weight: 500;
          font-style: normal;
          line-height: 22px;
        }
        .count-num {
          background: drop-shadow(0px 10px 30px rgba(0, 255, 167, 0.1));
          border: 1px solid #bfbfbf;
          box-sizing: border-box;
          border-radius: 5px;
          width: 45px;
          height: 35px;
          margin-left: 10px;
          margin-right: 10px;
          .num {
            text-align: center;
            font-size: 18px;
            font-style: normal;
            font-family: "Oswald";
            font-weight: 700;
            color: $white;
            margin-top: 3px;
          }
        }
        .minus,
        .plus {
          background: rgba(41, 45, 51, 0.3);
          border: 1px solid #8c8c8c;
          box-sizing: border-box;
          border-radius: 5px;
          width: 32px;
          height: 32px;
          &:active {
            background-color: #555555 !important;
            border-color: #8c8c8c !important;
          }
        }
        .minus-icon {
          background: url("../Assets/minus.png") no-repeat !important;
          position: relative;
          margin-left: -5px;
          margin-top: 8px;
          width: 14px;
          height: 14px;
        }
        .plus-icon {
          background: url("../Assets/plus.png") no-repeat !important;
          position: relative;
          margin-left: -5px;
          margin-top: 0px;
          width: 14px;
          height: 14px;
        }
      }
      .requestedTitle {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        margin-right: 16px;
      }
      .edit-button {
        color: $white;
        font-family: $fontfamilySecondary;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 21px;
        width: 140px;
        height: 32px;
        position: absolute;
        right: 20px;
        margin-top: 10px;
        z-index: 1;
        opacity: 1;
        &:hover {
          background: #3c99d0;
        }
        .edit-pen {
          background: url("../Assets/edit.png") no-repeat !important;
          margin-right: 10px;
          margin-left: 0px;
          width: 18px;
          height: 18px;
          display: inline-block;
        }
      }
    }
  }

  .cpr-icon {
    background: url("../Assets/cpr-icon.png") no-repeat;
    background-size: contain;
    margin-right: 14px;
    margin-left: 10px;
    padding-right: 20px;
  }
  .first-aid-icon {
    background: url("../Assets/first-aid-icon.png") no-repeat;
    background-size: contain;
    margin-right: 14px;
    margin-left: 10px;
    padding-right: 20px;
  }
  .basic-fire-icon {
    background: url("../Assets/basic-fire-icon.png") no-repeat;
    background-size: contain;
    margin-right: 14px;
    margin-left: 10px;
    padding-right: 20px;
  }
}
.assetManagementBox {
  .assetManagementBox-head-row {
    min-height: 55px;
    padding-left: 20px;
    padding-top: 11px;
    .assetManagementBox-title {
      color: $primary;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      margin-right: 75px;
    }
  }
  .assetManagementBox-content {
    min-height: 609px;
    margin-bottom: 22px;
    border: none;
  }
  .assetManagementBox-modified {
    min-height: 574px;
    margin-bottom: 16px;
    border: none;
  }
  .assetManagementMap-content {
    min-height: 590px;
    border: none;
    font-family: $fontfamilyPrimary;
  }
  .assetManagementMap-content-secondary {
    min-height: 351px;
    border: none;
    font-family: $fontfamilyPrimary;
  }
  .time-text {
    color: #ffffff;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
  }
  .content-text {
    color: #ffffff;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 24px;
  }
  .asset-map {
    background: url("../Assets/asset-map.svg") no-repeat;
    background-size: contain;
    width: 390px;
    height: 235px;
  }
  .toggleShowMap {
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #3c99d0;
    cursor: pointer;
    margin-left: 35%;
    &:hover {
      text-decoration: underline;
    }
  }

  .asset-call {
    background: url("../Assets/asset-call.svg") no-repeat;
    background-size: contain;
    width: 156px;
    height: 61px;
    float: right;
  }

  .accordion {
    border-bottom: 1px solid #555555;
    width: 435px;
    margin-left: -20px;
    margin-top: -15px;
    border-left: none;
    border-right: none;
    border-top: 1px solid #555555;
  }

  .accordion__item + .accordion__item {
    border-top: 1px solid #555555;
    //border-bottom: 1px solid #555555;
  }

  .accordion__button {
    background-color: #191a1c;
    color: $primary;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border-top: 1px solid #555555;
    border: none;
    display: block;

    .title-row {
      margin-bottom: 10px;
    }
    .title {
      font-family: $fontfamilyPrimary;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      font-style: normal;
    }
    .distance {
      float: right;
      margin-right: -25px;
    }

    .assetNumber {
      font-family: $fontfamilyPrimary;
      font-size: 20px;
      line-height: 24px;
      color: #b0b0b0;
      margin-left: 5px;
      margin-right: 30px;
    }
  }

  .accordion__button:before {
    float: right;
    color: #b0b0b0;

    content: "";
    height: 10px;
    width: 10px;
    margin-top: 38px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(45deg);
  }

  .accordion__button[aria-expanded="true"]::before,
  .accordion__button[aria-selected="true"]::after {
    transform: rotate(-135deg);
  }

  [hidden] {
    display: none;
  }

  .accordion__panel {
    padding: 20px;
    // animation: fadein 0.35s ease-in;
    border-top: 1px solid #262626;

    .head {
      margin-bottom: 5px;
    }
    .panel-block {
      height: 40px;
      width: 395px;
      padding: 5px;
      &.gray {
        background-color: #373737;
      }

      .title {
        font-family: $fontfamilyPrimary;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        font-style: normal;
      }

      .capacity {
        font-family: $fontfamilySecondary;
        font-weight: 500;
        font-size: 21px;
        line-height: 30px;
        margin-right: 5px;
        float: right;
        color: #bfbfbf;
        font-style: normal;
      }
    }
  }

  /* -------------------------------------------------- */
  /* ---------------- Animation part ------------------ */
  /* -------------------------------------------------- */

  @keyframes fadein {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }
}

