// Citizen Overview start here
.citizen-overview {
  position: relative;
  height: 413px;

  .scrollbar {
    height: 353px;
    overflow-y: scroll;
  }
  .text {
    color: $white;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: $fontsizePrimary;
    line-height: $secondarylineHeight;
  }
  .text-box-sm {
    width: 269px;
    height: 31px;
    background-color: #262626;
    padding: 5px;
  }
  .text-box-lg {
    width: 645px;
    height: 31px;
    background-color: #191a1c !important;
    border: 1px solid #555555;
    padding: 5px;
    color: $white !important;
  }
  .text-box-submit {
    width: 645px;
    height: 33px;
