}
.modal-content {
  .citizen-overview {
    height: 925px;
    margin-bottom: 50px;
  }
  .scrollbar {
    height: 916px;
    overflow-y: scroll;
  }
  .modal-content-center {
    height: 925px;
    align-items: center;
  }
  .content-border {
    border-bottom: 2px #555555 solid;
  }
  .summary-title {
    color: #ffffff;
    font-family: "Lato";
    font-style: normal;
    font-weight: 450;
    font-size: 24px;
    line-height: 30px;
    padding: 15px;
    //border-bottom: 2px #555555 solid;
    margin-bottom: 0;
  }
  .device-use {
    height: 100px;
  }

  /* switch sliders */
  .switch {
    position: relative;
    display: inline-block;
    width: 46px;
    height: 28px;
    input {
      opacity: 0;
      width: 0;
      height: 0;
    }
    input:checked + .slider {
      background-color: #2196f3;
    }
    input:focus + .slider {
      box-shadow: 0 0 1px #2196f3;
    }

    input:checked + .slider:before {
      -webkit-transform: translateX(18px);
      -ms-transform: translateX(18px);
      transform: translateX(18px);
    }
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #595959;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    &:before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: 0.4s;
      transition: 0.4s;
    }
  }

  .slider.round {
    border-radius: 34px;
    &:before {
      border-radius: 50%;
    }
  }
  /* increment decrement number */
  .text-counter {
    color: $white;
    font-size: $fontsizePrimary;
    font-family: $fontfamilyPrimary;
    font-weight: 500;
    font-style: normal;
    line-height: $secondarylineHeight;
    margin-top: 5px;
    margin-right: 10px;
  }

  .count-num {
    background: rgba(41, 45, 51, 0.3);
    border: 1px solid #8c8c8c;
    box-sizing: border-box;
    border-radius: 5px;
    width: 45px;
    height: 32px;
    margin-left: 10px;
    margin-right: 10px;
    .num {
      text-align: center;
      font-size: 18px;
      font-style: normal;
      font-family: "Oswald";
      font-weight: 700;
      color: $white;
      margin-top: 3px;
    }
  }
  .minus,
  .plus {
    background: rgba(41, 45, 51, 0.3);
    border: 1px solid #8c8c8c;
    box-sizing: border-box;
    border-radius: 5px;
    width: 32px;
    height: 32px;
    &:active {
      background-color: #555555 !important;
      border-color: #8c8c8c !important;
    }
  }
  .minus-icon {
    background: url("../Assets/minus.png") no-repeat !important;
    position: relative;
    margin-left: -5px;
    margin-top: 8px;
    width: 14px;
    height: 14px;
  }
  .plus-icon {
    background: url("../Assets/plus.png") no-repeat !important;
    position: relative;
    margin-left: -5px;
    margin-top: 0px;
    width: 14px;
    height: 14px;
  }
  .stat-box {
    height: 119px;
    .value {
      line-height: 97px;
    }
    .label {
      font-size: 14px;
      margin-top: -30px;
    }
  }
  .scan-text {
    color: $white;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: $secondarylineHeight;
    padding: 12%;
  }
  .code-img {
    background: url("../Assets/qr.png") no-repeat !important;
    position: relative;
    width: 119px;
    height: 119px;
  }
  .receipt-check {
    background: url("../Assets/receipient-check.png") no-repeat !important;
    position: relative;
    width: 28px;
    height: 29px;
    padding: 15px;
  }
  .event-sequence {
    background-color: #1e252c;
    border-radius: 2px;
    padding: 5%;
    display: flex;
    flex-direction: column;
    margin: 40px;
    //text-align: center;
    .title {
      color: $white;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: $secondarylineHeight;
    }
    .bottom-text {
      color: #bfbfbf;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      .borderline-left {
        background: url("../Assets/border-line.png") no-repeat !important;
        position: relative;
        width: 202px;
        height: 1px;
        right: 20px;
        top: -5px;
        display: inline-block;
      }
      .borderline-right {
        background: url("../Assets/border-line.png") no-repeat !important;
        position: relative;
        width: 202px;
        height: 1px;
        left: 20px;
        top: -5px;
        display: inline-block;
      }
    }
    .time-stamp {
      color: $white;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      //display: inline-block;
      //margin-left: 10px;
    }
    .message-log {
      color: #bfbfbf;
      font-family: $fontfamilyPrimary;
      font-style: normal;
      font-weight: 500;
      font-size: $fontsizePrimary;
      line-height: $secondarylineHeight;
      margin-left: 10px;
      width: 473px;
    }
  }
  .health-record {
    a {
      text-decoration: underline;
    }
  }
}
