@import "./Variables/variables.scss";

.threats-map {
  background-size: 100%;
  background: url("../Assets/threatsmap1.png") no-repeat center;
  //width: 780px;
  height: 500px;
  .icon-address-1 {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: $secondarylineHeight;
    position: absolute;
    top: 105px;
    left: 160px;
  }
  .icon-address-2 {
    color: $primary;
    font-family: $fontfamilyPrimary;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    position: absolute;
    top: 60px;
    right: 100px;
    width: 165px;
  }
}

.incident-threats {
  .cctv-box {
    position: absolute;
    width: 480px;
    height: 440px;
    left: 1420px;
    top: 160px;

    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .separator {
      width: 100%;
      height: 2px;
      background: #555555;
    }
    .title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .cctv-content {
      margin-top: 20px;
      margin-left: 20px;
      img {
        border-radius: 8px;
      }
      .entry {
        margin-left: 0px;
        margin-bottom: 20px;
        .container {
          width: 200px;
          margin: 20px;
        }
        .label {
          height: 30px;
          width: 64px;
        }
        .label-text {
          height: 30px;
          width: 64px;
          font-family: Teko;
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 29px;
          text-align: center;

          color: #ffffff;
        }
        .white-text {
          height: 30px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 27px;
          /* identical to box height */
          margin-left: 10px;
          display: flex;
          align-items: flex-end;

          color: #ffffff;
        }
        .big-white-text {
          height: 30px;
          font-family: Lato;
          font-style: normal;
          font-weight: 700;
          font-size: 20px;
          line-height: 27px;
          /* identical to box height */
          margin-left: 10px;
          display: flex;
          align-items: flex-end;
          color: #ffffff;
        }
        .grey-text {
          margin-top: 10px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 27px;

          color: #b0b0b0;
        }
      }
    }
  }
  .drone-box {
    position: absolute;
    width: 480px;
    height: 440px;
    left: 1420px;
    top: 620px;

    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .separator {
      width: 100%;
      height: 2px;
      background: #555555;
    }
    .title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .drone-content {
      margin: 20px;
      img {
        border-radius: 8px;
        margin-bottom: 20px;
      }
      .white-text {
        font-family: Lato;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 10px;
        color: #ffffff;
      }
      .entry {
        margin: 0px;
        width: 286px;
        height: 24px;
        justify-content: space-between;
        .metric {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;

          color: #ffffff;
        }
        .description {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          text-align: right;

          color: #b0b0b0;
        }
      }
    }
  }
  .threats-map {
    position: absolute;
    width: 880px;
    height: 500px;
    left: 520px;
    top: 160px;
  }
  .predicted-box {
    position: absolute;
    width: 880px;
    height: 380px;
    left: 520px;
    top: 680px;

    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .separator {
      width: 100%;
      height: 2px;
      background: #555555;
    }
    .bottom-separator {
      position: absolute;
      width: 100%;
      height: 2px;
      background: #555555;
      margin-top: 10px;
    }
    .threats-nav {
      margin-top: 25px;
      margin-left: 10px;
    }
    .title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .predicted-content {
      margin: 20px;
      .entry {
        margin: 0px;
        .red-text {
          margin-bottom: 10px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 27px;
          display: flex;
          align-items: flex-end;
          color: #ff0000;
        }
        .white-text {
          margin-bottom: 10px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 27px;
          display: flex;
          align-items: flex-end;
          color: #fff;
        }
        .grey-text {
          margin-bottom: 10px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 24px;
          /* or 120% */

          display: flex;
          align-items: flex-end;

          color: #b0b0b0;
        }
      }
    }
    .accept-recommendations-button {
      border-color: #222d39;
      margin-left: 450px;
      margin-top: 32px;
      width: 420px;
      height: 32px;
      background: #222d39;
      border-radius: 4px;
      .button-text {
        /* Teko-Medium */
        margin-top: -5px;
        font-family: Teko;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 34px;
        text-align: center;

        color: #ffffff;
      }
    }
  }
  .sensor-box {
    position: absolute;
    width: 480px;
    height: 210px;
    left: 20px;
    top: 850px;

    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .separator {
      width: 478px;
      height: 2px;
      background: #555555;
    }
    .title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .sensor-content {
      margin-left: 20px;
      margin-top: 28px;
    }
  }
  .weather-box {
    position: absolute;
    width: 480px;
    height: 210px;
    left: 20px;
    top: 620px;
    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .weather-separator {
      width: 478px;
      height: 2px;
      background: #555555;
    }
    .weather-title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .weather-content {
      padding-top: 34px;
      margin-left: 40px;
      margin-right: 20px;
      .entry {
        width: 210px;
        height: 24px;
        justify-content: space-between;

        .metric {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;

          color: #ffffff;
        }
        .description {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          text-align: right;
          width: auto;

          color: #b0b0b0;
        }
        .yellow-description {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          text-align: right;

          color: #fffe54;
        }
      }
    }
  }
  .activity-log-box {
    position: absolute;
    width: 480px;
    height: 440px;
    left: 20px;
    top: 160px;
    background: #191a1c;
    border: 2px solid #555555;
    box-sizing: border-box;
    .activity-log-separator {
      width: 478px;
      height: 2px;
      background: #555555;
    }
    .activity-log-title {
      /* Activity Log */
      height: 32px;
      margin-left: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: Lato;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 33px;
      text-transform: capitalize;

      color: #ffffff;
    }
    .activity-log-content {
      padding: 20px;
      .entry {
        .time {
          margin-left: 20px;
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          display: flex;
          align-items: flex-start;

          color: #ffffff;
        }
        .description {
          font-family: Lato;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          display: flex;
          align-items: flex-end;
          word-wrap: break-word;
          width: 368px;
          color: #b0b0b0;
        }
      }
    }
  }
  .greentext {
    font-family: Teko;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: $primary;
  }

  .redtext {
    font-family: Teko;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: red;
  }

  .whitetext {
    font-family: Teko;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: white;
  }

  .text {
    font-family: Teko;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
  }

  .card {
    border: 20px solid transparent;
    padding: 15px;
    border-image: url("../Assets/card.svg");
    width: 100%;
    background-color: $backgroundColor;
    border-image-slice: 10% 10%;
    margin: 10px;
  }

  .inner-card {
    border: 3px solid $primary;
    box-sizing: border-box;
    border-radius: 16px;
    padding: 10px 10px;
    margin-bottom: 24px;
    height: 360px;
    background-color: #1e252c;
  }

  .activity-log-generic {
    margin: -10px;
    .activity-log-title {
      color: $primary;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
    }

    .activity-log-time {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      display: inline;
      width: 100px;
    }

    .activity-log-desc {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      display: inline;
      width: 320px;
    }

    .evacuation-analysis-title {
      color: $primary;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
      padding-bottom: 20px;
    }

    .evacuation-analysis-list-title {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $secondarylineHeight;
      display: inline;
      width: 100px;
    }

    .evacuation-analysis-list {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $secondarylineHeight;
      display: inline;
      width: 100px;
    }

    .evacuation-analysis-left {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $secondarylineHeight;
      display: inline;
      width: 100px;
    }

    .span-after-logo {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: 43px;
      display: inline;
      width: 100px;
    }

    .last-span {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $primarylineHeight;
      display: inline;
      width: 100px;
      margin-left: 10px;
    }

    .myresponders-last-span {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $primarylineHeight;
      display: inline;
      width: 100px;
      margin-left: 10px;
    }

    .operating-procedure-left {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: $secondarylineHeight;
      display: inline;
      width: 80px;
    }

    .evacuation-analysis-right {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: 20px;
      float: right;
    }
  }

  .activity-cctv {
    margin: -15px;
    position: relative;

    .activity-cctv-title {
      color: $primary;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
    }

    .activity-cctv-content {
      .activity-cctv-img-1 {
        background: url("../Assets/cctv1.png") no-repeat;
        width: 200px;
        height: 100px;
      }

      .activity-cctv-img-2 {
        background: url("../Assets/cctv2.png") no-repeat;
        width: 200px;
        height: 100px;
      }

      .activity-cctv-lv {
        max-width: 150px;
        margin-left: 20px;
      }

      .lv-button {
        background: url("../Assets/LVButton.svg") no-repeat;
        width: 80px;
        height: 40px;
      }

      .lv-button div {
        color: $backgroundColor;
        font-family: "Teko";
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 40px;
        text-align: center;
      }

      .text-cctv {
        color: $primary;
        font-family: "Teko";
        font-style: normal;
        font-weight: 500;
        font-size: 20px;

        .inner-card {
          background: #1e252c;
        }
      }
    }
  }

  .activity-log {
    margin: -10px;
    .activity-log-title {
      color: $primary;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
    }
    .greenbutton {
      font-family: Teko;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      color: black;
      background-color: $primary;
      border-color: $primary;
    }
    .white-text {
      color: $white;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      display: inline;
      width: 100px;
    }

    .activity-log-desc {
      color: $primary;
      font-family: "Teko";
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      display: inline;
      width: 320px;
    }
  }

  .side-col {
    max-width: 480px;
  }

  .activity-drone {
    margin-top: -20px;
    margin-bottom: -10px;
    .activity-cctv-title {
      color: $primary;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
    }
